import { Colored, Main, PageWrapper, Section } from '../components/utils';
import Header from '../components/Header';
import BlockQuote from '../components/BlockQuote';
import { useState } from 'react';
import RadioGroup from '../components/forms/RadioGroup';
import { Option } from '../components/forms/types';

const question = {
  id: 'storage-type',
  title: 'What do we store',
  type: 'radio',
  description:
    'Unlike normal file storage, webapps like React and Vue have a default path, and error pages.',
  options: [
    { value: 'webapp', label: 'A webapp' },
    { value: 'files', label: 'Files' },
  ],
};

export default function Home() {
  const [selectedOption, setSelectedOption] = useState<Option | null>(null);
  return (
    <PageWrapper>
      <Header />
      <Main>
        <BlockQuote
          author={'Lolrus, the bucket walrus'}
          source={'At the launch of AWS S3 in 2006'}>
          <>
            Hosting a<Colored> webapp </Colored>
            or need to store uploads? Put them in S3 buckets. It&apos;s
            <Colored> cheap </Colored>
            and
            <Colored> reliable</Colored>. Let me show how
            <Colored> easy </Colored>
            it is.
          </>
        </BlockQuote>
      </Main>
      <Section>
        <RadioGroup
          options={question.options}
          selectedOption={selectedOption}
          onChange={setSelectedOption}
          title={question.title}
          description={question.description}
        />
      </Section>
    </PageWrapper>
  );
}
